
$div-width: 75%;
$white-background: white;
$box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em,
  rgba(43, 75, 134, 0.05) 0px 0.25em 1em;
$border-radius: 5px;
$color1: #0d3b6d;
$color2: #ec7424;
$width-100: 100%;

body {
  background-color: rgb(248, 248, 248);
}

p,
ul {
  margin: 0;
  padding: 0 !important;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px $color1;
  border-radius: $border-radius;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-clip: content-box;
  background: $color1;
  border-radius: $border-radius;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $color1;
}

//Navbar

.navbar {
  background-color: rgb(0, 132, 255);
  .container-fluid {
    width: 75%;
    margin-inline: auto;
  }
  // .navbar-brand{
  //     font-size: 2vw;
  // }

  .navbar-brand {
    font-size: 23px;
  }

  .nav-link {
    font-size: 17px;
  }

  a {
    color: white !important;
  }
  .footer-social {
    ul {
      display: flex;
      padding: 0;
    }

    li {
      height: 40px;
      line-height: 40px;
      width: 40px;
      border-radius: 100%;
      justify-content: center;
      align-items: center;
      display: flex;
      -webkit-transition: -webkit-transform 0.8s ease-in-out;
      -ms-transition: -ms-transform 0.8s ease-in-out;
      transition: transform 0.8s ease-in-out;
      margin: 0 1.5% 4px 0;

      &:hover {
        transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
      }
      a{
        text-align: center;  
      }
    }
  }
}


// Home Part

.home {
  font-style: italic;

  .home-header {
    width: $width-100;
    text-align: center;
    padding-block: 4%;
    // font-size: 2vw;
    font-size: 40px;
    margin-top: 20px;
    color: rgb(201, 50, 50);
    background-color: $white-background;
    background-image: url("https://mdbcdn.b-cdn.net/img/Photos/Slides/img%20(15).webp");
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
  }

  .home-body {
    width: $div-width;
    margin: 20px auto;
    background-color: $white-background;
    display: flex;

    .home-body-part-2 {
      background-color: rgba(252, 252, 252);
      text-align: center;
      padding-top: 100px !important;

      h2 {
        color: $color2;
      }

      p {
        color: $color1;
      }
    }

    .home-body-part,
    .home-body-part-2 {
      width: 50%;
      padding: 10px 15px;

      ul {
        font-size: 20px;

        li {
          padding: 10px;
          margin-block: 10px;
          box-shadow: $box-shadow;
          border-radius: $border-radius;
          span {
            margin-right: 10%;
          }
        }
      }
    }
  }

  .last-added {
    width: $div-width;
    margin: auto;
    background-color: $white-background;
    padding: 2%;
    max-height: 400px;
    min-height: 300px;
    overflow-y: scroll;

    h2 {
      text-align: center !important;
      margin-bottom: 25px;
      color: $color2;
    }

    ul {
      width: $width-100;

      li {
        display: flex;
        margin-bottom: 10px;

        div {
          width: 49%;
          padding: 15px 10px;
          box-shadow: $box-shadow;
          border-radius: $border-radius;

          &:nth-child(2) {
            margin-left: 2%;
          }

          span {
            margin-right: 10%;
            font-size: 20px;
          }

          p {
            color: black;
            width: 75%;
          }
        }
      }
    }
  }
}

// Notes General Page

.notes-part {
  font-style: italic;

  .notes-part-header {
    width: $width-100;
    padding-block: 8%;
    // font-size: 2vw;
    margin-top: 20px;
    color: rgb(201, 50, 50);
    background-color: $white-background;
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover;
  }

  .notes-part-body {
    width: $div-width;
    display: flex;
    padding: 25px;
    margin: auto;
    background-color: white;
    margin-block: 30px;

    .notes-detail-left-part, .notes-part-left{
      width: 60%;
      margin-right: 15%;
    }

    .notes-part-left {

      img{
        width: 100%;
      }
      
      li {
        padding: 15px;
        box-shadow: $box-shadow;
        border-radius: $border-radius;
        margin-bottom: 10px;

        p {
          color: black;
          width: $div-width;
        }
      }
    }

    .notes-part-right {
      width: 25%;
      box-shadow: $box-shadow;
      border-radius: $border-radius;
      padding: 10px;

      i {
        margin-left: 20px;
      }

      li {
        margin-block: 20px;
      }
    }
  }
}

// English Part

.english-test-body{
  padding: 10px;

  input{
    margin-top: 20px;
  }

  label{
    margin-left: 5px;
  }

  button{
    margin-top: 20px;
  }
  
}

//Footer
.footer {
  padding-block: 10px;
  margin-top: 30px;
  background-color: #6d83ad;   
  color: rgb(255, 255, 255);
  font-size: 16px;
}
